.node rect {
    cursor: move;
    fill-opacity: .9;
    shape-rendering: crispEdges;
    fill : #fff;
  }
  
  .node text {
    fill : #fff;
    pointer-events: none;
    font-size:20px
  }
  
  .link {
    fill: none;
    stroke: #db0000;
    stroke-opacity: .2;
  }
  
  .link:hover {
    stroke-opacity: 0.5;
  }